
*{margin:0;padding:0;outline:0
}
html,body{height:100%
}
body{margin:0;font-size:2rem;font-family:-apple-system, BlinkMacSystemFont, 'avenir next', avenir, helvetica, 'helvetica neue', Ubuntu, 'segoe ui', arial, sans-serif;background-color:#e7ebf3
}
#app{height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.zpm-loading{position:fixed;left:0;top:0;width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;background-color:transparent
}
.zpm-loading img{width:80px
}



